    border-radius: 4px 0 0 4px;
    -webkit-box-shadow: none !important;
            box-shadow: none !important;
}
.el-radio-button__orig-radio {
    opacity: 0;
    outline: none;
    position: absolute;
    z-index: -1;
}
.el-radio-button__orig-radio:checked + .el-radio-button__inner {
